<template><div class="markdown-body"><h1>底部导航</h1>
<p><code v-pre="">mu-bottom-navigation</code> 用于底部的导航组件。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-bottom-nav&gt;\n    &lt;mu-bottom-nav-item title=\&quot;Recents\&quot; icon=\&quot;restore\&quot;&gt;&lt;/mu-bottom-nav-item&gt;\n    &lt;mu-bottom-nav-item title=\&quot;Favorites\&quot; icon=\&quot;favorite\&quot;&gt;&lt;/mu-bottom-nav-item&gt;\n    &lt;mu-bottom-nav-item title=\&quot;Nearby\&quot; icon=\&quot;location_on\&quot;&gt;&lt;/mu-bottom-nav-item&gt;\n  &lt;/mu-bottom-nav&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-bottom-nav>
    <mu-bottom-nav-item title="Recents" icon="restore"></mu-bottom-nav-item>
    <mu-bottom-nav-item title="Favorites" icon="favorite"></mu-bottom-nav-item>
    <mu-bottom-nav-item title="Nearby" icon="location_on"></mu-bottom-nav-item>
  </mu-bottom-nav>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Recents"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"restore"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Favorites"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"favorite"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Nearby"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"location_on"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="shift-mode"><a class="header-anchor" href="#shift-mode" aria-hidden="true">¶</a> Shift Mode</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container style=\&quot;max-width: 400px; width:100%;\&quot;&gt;\n  &lt;mu-bottom-nav :value.sync=\&quot;shift\&quot; shift&gt;\n    &lt;mu-bottom-nav-item value=\&quot;movies\&quot; title=\&quot;Movies\&quot; icon=\&quot;ondemand_video\&quot;&gt;&lt;/mu-bottom-nav-item&gt;\n    &lt;mu-bottom-nav-item value=\&quot;music\&quot; title=\&quot;Music\&quot; icon=\&quot;music_note\&quot;&gt;&lt;/mu-bottom-nav-item&gt;\n    &lt;mu-bottom-nav-item value=\&quot;books\&quot; title=\&quot;Books\&quot; icon=\&quot;books\&quot;&gt;&lt;/mu-bottom-nav-item&gt;\n    &lt;mu-bottom-nav-item value=\&quot;pictures\&quot; title=\&quot;Pictures\&quot; icon=\&quot;photo\&quot;&gt;&lt;/mu-bottom-nav-item&gt;\n  &lt;/mu-bottom-nav&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      shift: 'movies'\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container style="max-width: 400px; width:100%;">
  <mu-bottom-nav :value.sync="shift" shift="">
    <mu-bottom-nav-item value="movies" title="Movies" icon="ondemand_video"></mu-bottom-nav-item>
    <mu-bottom-nav-item value="music" title="Music" icon="music_note"></mu-bottom-nav-item>
    <mu-bottom-nav-item value="books" title="Books" icon="books"></mu-bottom-nav-item>
    <mu-bottom-nav-item value="pictures" title="Pictures" icon="photo"></mu-bottom-nav-item>
  </mu-bottom-nav>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"max-width: 400px; width:100%;"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav</span> <span class="hljs-attr">:value.sync</span>=<span class="hljs-string">"shift"</span> <span class="hljs-attr">shift</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav-item</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"movies"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Movies"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"ondemand_video"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav-item</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"music"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Music"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"music_note"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav-item</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"books"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Books"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"books"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-nav-item</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"pictures"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Pictures"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"photo"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-nav</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">shift</span>: <span class="hljs-string">'movies'</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="bottom-nav-props"><a class="header-anchor" href="#bottom-nav-props" aria-hidden="true">¶</a> Bottom Nav Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>导航颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>shift</td>
<td>是否使用 shift 模式</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>配合 bottom-nav-item 使用， 可使用 <code v-pre="">.sync</code> 修饰符进行双向绑定</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="bottom-nav-events"><a class="header-anchor" href="#bottom-nav-events" aria-hidden="true">¶</a> Bottom Nav Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当被激活的value 改变时触发</td>
<td>(value)</td>
</tr>
</tbody>
</table>
<h2 id="bottom-nav-item-props"><a class="header-anchor" href="#bottom-nav-item-props" aria-hidden="true">¶</a> Bottom Nav Item Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>icon</td>
<td>图标</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>title</td>
<td>显示的标题</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>value</td>
<td>当value 与父组件的 value值相等时，会变成选中状态</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>ripple</td>
<td>是否开启波纹效果</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>href</td>
<td>相当于 a 标签的 href属性，设置之后会渲染成 a 标签，默认是使用button标签</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>to</td>
<td>表示目标路由的链接。设置之后组件会被渲染成<code v-pre="">router-link</code>, 当被点击后，内部会立刻把 <code v-pre="">to</code> 的值传到 <code v-pre="">router.push()</code>，所以这个值可以是一个字符串或者是描述目标位置的对象。</td>
<td>String/Object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>tag</td>
<td>有时候想要 <code v-pre="">&lt;router-link&gt;</code> 渲染成某种标签，例如 <code v-pre="">&lt;li&gt;</code>。 于是我们使用 <code v-pre="">tag prop</code> 类指定何种标签，同样它还是会监听点击，触发导航。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>active-class</td>
<td>设置 链接激活时使用的 CSS 类名。默认值可以通过路由的构造选项 <code v-pre="">linkActiveClass</code> 来全局配置。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>event</td>
<td>声明可以用来触发导航的事件。可以是一个字符串或是一个包含字符串的数组</td>
<td>String/Array</td>
<td>—</td>
<td>click</td>
</tr>
<tr>
<td>exact</td>
<td>“是否激活” 默认类名的依据是 <strong>inclusive match</strong> （全包含匹配）。 举个例子，如果当前的路径是 <code v-pre="">/a</code> 开头的，那么 <code v-pre="">&lt;router-link to=&quot;/a&quot;&gt;</code> 也会被设置 CSS 类名。</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>exact-active-class</td>
<td>配置当链接被精确匹配的时候应该激活的 class。注意默认值也是可以通过路由构造函数选项 linkExactActiveClass 进行全局配置的。</td>
<td>String</td>
<td>—</td>
<td>router-link-exact-active</td>
</tr>
<tr>
<td>append</td>
<td>设置 <code v-pre="">append</code> 属性后，则在当前（相对）路径前添加基路径。例如，我们从 <code v-pre="">/a</code> 导航到一个相对路径 b，如果没有配置 append，则路径为 /b，如果配了，则为 /a/b</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>replace</td>
<td>设置 <code v-pre="">replace</code> 属性的话，当点击时，会调用 <code v-pre="">router.replace()</code> 而不是 <code v-pre="">router.push()</code>，于是导航后不会留下 history 记录。</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      bottomNav: 'recents',
      shift: 'movies'
    }
  }
}
</script>


